@import './variable';

.fk-modal {
  .modal-content {
    @extend .fk-container;
  }

  .header {
    border-bottom: $border;
    font-size: $font-size-xl;
    font-weight: $font-weight-xl;
    line-height: 1;
  }

  .button-close {
    border: none;
    color: $snow;
    background: transparent;
    transition: none;
    &:hover,
    &:focus,
    &:focus-visible {
      box-shadow: none;
      border: none;
      color: $snow;
      background: transparent;
      box-shadow: none;
      outline: none;
      .icon-close {
        color: $snow;
      }
    }
  }

  .icon-close {
    color: $stone;
  }

  .body {
  }

  .footer {
  }
}
